$(".burger").click(function () {
    showMenu()
    $(".header").addClass("_menu-open")
})

$(".search-toggle").click(function () {
    showMenu()
    $(".header").addClass("_search-open")
})

$("._close-btn").click(function () {
    hideMenu()
})

function showMenu() {
    $(".header").addClass("_open")
    $("body").addClass("fixed-body")
}

function hideMenu() {
    $(".header").removeClass("_open")
    $(".header").removeClass("_menu-open")
    $(".header").removeClass("_search-open")
    $("body").removeClass("fixed-body")
}
